import { Box, Button, Container, Grid, Typography } from '@mui/material';
import classNames from 'classnames';
import gsap from 'gsap/dist/gsap';
import React, { useEffect } from 'react';
import styles from './style.module.scss';
import mmLogo from '../../assets/icons/mm.svg';
import { Link } from 'react-scroll';
import { useTranslation, withTranslation } from 'react-i18next';

const Hero = () => {
  const { t } = useTranslation('translation', { keyPrefix: 'hero' });

  useEffect(() => {
    const ctx = gsap.context(() => {
      gsap.to('.hero-title', {
        y: 0,
        opacity: 1,
        delay: 0.4
      });

      gsap.to('.hero-description', {
        y: 0,
        opacity: 1,
        delay: 0.8
      });

      gsap.from('.hero-button', {
        opacity: 0,
        delay: 1.2
      });
    });

    return () => ctx.revert();
  }, []);
  return (
    <Box className={classNames(styles.container, 'hero')}>
      <Container>
        <Grid container>
          <Grid sx={6}>
            <Box className={styles.content}>
              <img src={mmLogo} alt="logo" className={styles.img} />
              <Typography
                variant="h1"
                className="hero-title"
                style={{
                  opacity: 0,
                  transform: 'translate(0, 120px)'
                }}
              >
                Move Money Capital (MMC)
              </Typography>
              <Typography
                variant="h2"
                className="hero-description"
                style={{
                  opacity: 0,
                  transform: 'translate(0, 120px)'
                }}
              >
                {t('description')}
              </Typography>
              <Button variant="outlined" className="hero-button">
                <Link
                  to={'contact'}
                  spy={true}
                  smooth={true}
                  offset={-120}
                  duration={800}
                >
                  {t('action')}
                </Link>
              </Button>
            </Box>
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
};

export default withTranslation()(Hero);
