import { Box, Container, Typography, useMediaQuery } from '@mui/material';
import { useTheme } from '@mui/material/styles';
import React, { useEffect } from 'react';
import Member from './Member';
import styles from './style.module.scss';
import member1Img from '../../assets/icons/person_hover3.svg';
import member2Img from '../../assets/icons/person2.svg';
import member3Img from '../../assets/icons/person3.svg';
import member4Img from '../../assets/icons/person4.svg';
import member5Img from '../../assets/icons/person5.svg';
import hover1Img from '../../assets/icons/person_hover9.svg';
import hover2Img from '../../assets/icons/person_hover8.svg';
import hover3Img from '../../assets/icons/person_hover2.svg';
import hover4Img from '../../assets/icons/person_hover4.svg';
import hover5Img from '../../assets/icons/person_hover5.svg';
import classNames from 'classnames';
import { gsap } from 'gsap/dist/gsap';
import { useTranslation, withTranslation } from 'react-i18next';

const members = [
  {
    name: 'MJ',
    imgSrc: member2Img,
    hoverSrc: hover2Img,
    tooltipPosition: 'top-right',
    tooltip: `Welcome to MMC!`,
    staff: 'CEO <br/> Co-founder'
  },
  {
    name: 'Cindy',
    imgSrc: member3Img,
    hoverSrc: hover3Img,
    tooltipPosition: 'top-left',
    tooltip: `Hi 👋`,
    staff: 'COO <br/> Co-founder'
  },
  {
    name: 'Jason',
    imgSrc: member1Img,
    hoverSrc: hover1Img,
    tooltipPosition: 'top-left1',
    tooltip: ` I can't wait to meet you`,
    staff: 'Research'
  },

  {
    name: 'Alice',
    imgSrc: member4Img,
    hoverSrc: hover4Img,
    tooltipPosition: 'top-left2',
    tooltip: `Awesome 😍`,
    staff: 'Research'
  },
  {
    name: 'Brian',
    imgSrc: member5Img,
    hoverSrc: hover5Img,
    tooltipPosition: 'top-left3',
    tooltip: `Have a wonderful holiday season ☃️`,
    staff: 'Analysis'
  }
];

const Teams = () => {
  const theme = useTheme();
  const isMatches = useMediaQuery(theme.breakpoints.down('sm'));
  const { t } = useTranslation('translation');

  useEffect(() => {
    if (isMatches || !isMatches) return;

    const ctx = gsap.context(() => {
      gsap.from('.member1', {
        scrollTrigger: {
          trigger: '.member1',
          toggleActions: 'play reverse play reverse',
          start: `1000s`,
          end: `2000000000s`
        },
        y: -80,
        opacity: 0
      });

      gsap.from('.member2', {
        scrollTrigger: {
          trigger: '.member2',
          toggleActions: 'play reverse play reverse',
          start: `2500s`,
          end: `4000000000s`
        },
        y: -80,
        opacity: 0
      });

      gsap.from('.member3', {
        scrollTrigger: {
          trigger: '.member3',
          toggleActions: 'play reverse play reverse',
          start: `3500s`,
          end: `6000000000s`
        },
        y: -80,
        opacity: 0
      });

      gsap.from('.member4', {
        scrollTrigger: {
          trigger: '.member4',
          toggleActions: 'play reverse play reverse',
          start: `4500s`,
          end: `8000000000s`
        },
        y: -80,
        opacity: 0
      });

      gsap.from('.member5', {
        scrollTrigger: {
          trigger: '.member5',
          toggleActions: 'play reverse play reverse',
          start: `5500s`,
          end: `8000000000s`
        },
        y: -80,
        opacity: 0
      });

      gsap.to('.teams', {
        scrollTrigger: {
          pin: '.teams',
          end: `${
            Array.from([...members.map((_, index) => index + 1)]).length *
              1000 *
              1.8 -
            1000
          }s`,
          pinSpacing: true
        }
      });
    });

    return () => ctx.revert();
  }, [isMatches]);

  return (
    <Box className={classNames(styles.container, 'teams')}>
      <Container className={styles.wrapper}>
        <Typography className={classNames(styles.title, 'team-title')}>
          {t('our_team')}
        </Typography>
        <Box display="flex" className={styles.members}>
          {members.map((member, index) => (
            <Box
              key={index}
              className={classNames(
                styles['member' + (index + 1)],
                `member${index + 1}`,
                styles.member
              )}
            >
              <Member {...member} />
            </Box>
          ))}
        </Box>
      </Container>
    </Box>
  );
};

export default withTranslation()(Teams);
