import { Box, Button, Container, Typography } from '@mui/material';
import classNames from 'classnames';
import React, { useState } from 'react';
import styles from './style.module.scss';
import { withTranslation, useTranslation } from 'react-i18next';

const Coorporate = () => {
  const { t } = useTranslation('translation', { keyPrefix: 'contact' });

  const [open, setOpen] = useState(false);
  const [isCopied, setIsCopied] = useState(false);

  const handleCopy = () => {
    navigator.clipboard.writeText('cs@movemoney.capital');
    setIsCopied(true);
  };

  return (
    <Box className={styles.container}>
      <Container>
        <Box
          display="flex"
          alignItems="center"
          justifyContent="center"
          flexDirection="column"
        >
          <Typography className={styles.title}>{t('title')}</Typography>
          <Typography
            className={styles.description}
            dangerouslySetInnerHTML={{ __html: t('description') }}
          />
          {!open && (
            <Button variant="outlined" onClick={() => setOpen((prev) => !prev)}>
              cs@movemoney.capital
            </Button>
          )}
          {open && (
            <Box display="flex" flexDirection="column" mt={3}>
              <Button
                variant="outlined"
                className={classNames({ [styles.copied]: isCopied })}
                onClick={handleCopy}
              >
                {isCopied ? t('copied') : t('copy')}
              </Button>
              <a href="mailto:cs@movemoney.capital">
                <Button variant="outlined">{t('send')}</Button>
              </a>
            </Box>
          )}
        </Box>
      </Container>
    </Box>
  );
};

export default withTranslation()(Coorporate);
