import * as React from 'react';
import PropTypes from 'prop-types';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import IconButton from '@mui/material/IconButton';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import { Container } from '@mui/material';
import styles from './style.module.scss';
import classNames from 'classnames';
import { Link } from 'react-scroll';
import { useTranslation, withTranslation } from 'react-i18next';
import LanguagePicker from '../../components/LanguagePicker';

const drawerWidth = 240;
const navItems = [
  {
    title: 'About',
    selector: 'about'
  },

  {
    title: 'Portfolio',
    selector: 'portfolio'
  },
  {
    title: 'Teams',
    selector: 'team'
  },

  {
    title: 'Contact',
    selector: 'contact'
  }
];

function Header(props) {
  const { window } = props;
  const { t } = useTranslation('translation', { keyPrefix: 'header' });

  const [mobileOpen, setMobileOpen] = React.useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen((prevState) => !prevState);
  };

  const drawerStyle = {
    display: { xs: 'block', sm: 'none' },
    '& .MuiDrawer-paper': {
      boxSizing: 'border-box',
      width: drawerWidth,
      height: '100vh',
      left: 'auto',
      top: 80,
      right: 0,
      transform: mobileOpen
        ? 'translateX(0)!important'
        : 'translateX(225px)!important',
      transition: 'transform 225ms cubic-bezier(0, 0, 0.2, 1) 0.5ms'
    }
  };

  const container =
    window !== undefined ? () => window().document.body : undefined;

  const drawer = (
    <Box sx={{ textAlign: 'center' }}>
      <List
        style={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'flex-end'
        }}
      >
        {navItems.map((item) => (
          <Link
            key={item.title}
            activeClass={styles.active}
            className={item.selector}
            to={item.selector}
            spy={true}
            smooth={true}
            offset={-120}
            duration={800}
            onClick={handleDrawerToggle}
          >
            <ListItem disablePadding>
              <ListItemButton sx={{ textAlign: 'right', mr: 5 }}>
                <ListItemText
                  primary={t(item.selector)}
                  style={{
                    'font-weight': 500,
                    'font-size': 36,
                    'line-height': '42px'
                  }}
                />
              </ListItemButton>
            </ListItem>
          </Link>
        ))}
        <ListItem
          disablePadding
          style={{
            borderTop: '2px solid rgba(0,0,0,0.4)',
            width: '80%',
            marginRight: 20,
            paddingRight: 15,
            paddingTop: 30
          }}
        >
          <ListItemButton
            sx={{
              textAlign: 'right'
            }}
          >
            <hr />
            <LanguagePicker />
          </ListItemButton>
        </ListItem>
      </List>
    </Box>
  );

  return (
    <Box sx={{ display: 'flex' }}>
      <AppBar component="nav" className={styles.container}>
        <Container className={styles.wrapper}>
          <Toolbar className={styles.toolbar}>
            <Typography variant="h6" component="div" className={styles.logo}>
              <a href="/" style={{ color: '#000', textDecoration: 'none' }}>
                Move Money Capital
              </a>
            </Typography>
            <IconButton
              color="inherit"
              aria-label="open drawer"
              edge="start"
              onClick={handleDrawerToggle}
              sx={{ display: { sm: 'none' } }}
            >
              <div
                className={classNames(
                  {
                    [styles['acitve']]: mobileOpen
                  },
                  styles['hamburger-lines']
                )}
              >
                <span
                  className={classNames(styles['line'], styles['line1'])}
                ></span>
                <span
                  className={classNames(styles['line'], styles['line2'])}
                ></span>
                <span
                  className={classNames(styles['line'], styles['line3'])}
                ></span>
              </div>
            </IconButton>
            <Box sx={{ display: { xs: 'none', sm: 'flex' } }}>
              {navItems.map((item) => (
                <Button className={styles.link} key={item.title} disableRipple>
                  <Link
                    activeClass={styles.active}
                    className={item.selector}
                    to={item.selector}
                    spy={true}
                    smooth={true}
                    offset={-120}
                    duration={800}
                  >
                    <Typography>{t(item.selector)}</Typography>
                  </Link>
                </Button>
              ))}
              <Box display="flex" mx={2} alignItems="flex-start" pt="0">
                <LanguagePicker />
              </Box>
            </Box>
          </Toolbar>
        </Container>
      </AppBar>
      <Box component="nav">
        <Drawer
          container={container}
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true // Better open performance on mobile.
          }}
          sx={drawerStyle}
        >
          {drawer}
        </Drawer>
      </Box>
    </Box>
  );
}

Header.propTypes = {
  window: PropTypes.func
};

export default withTranslation()(Header);
